<template>
  <div id="topnavbar">
    <nav class="navbar navbar-dark bg-dark py-0 flex-column" v-if="!showEmpty">
      <a class="navbar-brand" href="#/">
        <img class="brand-image" src="@/assets/logo.png">
      </a>
      <div class="expand navbar-expand" id="navbarsExample04">
        <ul class="navbar-nav mr-auto flex-column">
          <li class="nav-item" v-for="item in Object.keys(topModules)" :key="item">
            <router-link class="nav-link" :to="{name: item}" >
                <svg class="icon svg-icon" aria-hidden="true">
                  <use :xlink:href="'#'+topModuleIcons[item]"></use>
                </svg>
                {{topModules[item]}}
            </router-link>
          </li>
        </ul>
      </div>
    </nav>
  </div>
</template>

<script>
import { getToken, removeToken } from '@/utils/auth'
import utils from '@/utils/utils'
import algorithmUtils from './modules/algorithm-utils'
import apis from '@/apis/apis'

export default {
  name: 'TopNavBar',
  components: {
  },
  data () {
    return {
      loginModalId: utils.randomId('modalId'),
      topModules: {
        'ModuleDashboard': '',
        'ModuleAlerts': '',
        // 'ModuleAlertConfig': '',
        'ModuleChConfig': '',
        'ModuleAlgorithm': '',
        'ModuleSysConfig': '',
      },
      topModuleIcons: {
        'ModuleDashboard': 'icon-yibiaopan',
        'ModuleAlerts': 'icon-baojing',
        // 'ModuleAlertConfig': 'icon-shangchuandaochu',
        'ModuleChConfig': 'icon-jiankongshexiangtou-xianxing',
        'ModuleAlgorithm': 'icon-sanweimoxing',
        'ModuleSysConfig': 'icon-shezhi',
      },
      showEmpty: false,
      loggedIn: getToken()
    }
  },
  methods: {
    getModuleName (routeBase, name) {
      return (
        routeBase + name
      )
    },
    logout () {
      removeToken()
      this.loggedIn = getToken()
    },
    loadAlgorithm () {
      let endpoint = 'algorithm/algorithms/'

      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0) {
            // 
            let algorithms = {}
            for (let i in res.data) {
              algorithms[res.data[i].name] = {name_ch: res.data[i].name_ch}
            }
            algorithmUtils.saveAlgorithms(algorithms)
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    }
  },
  computed: {
  },
  watch: {
  },
  beforeMount () {
    // console.log('beforeMount this.$route: ', this.$route)
  },
  mounted () {
    // console.log('mounted this.$route: ', this.$route)
    this.loadAlgorithm()
  }
}
</script>

<style scoped>
  .brand-image {
    width: 50px;
    height: auto;
  }
</style>
